<template>
    <nav class="navbar topbar_nav">
    <div class="container-fluid col-sm-11 col-sm-offset-1">
        <div class="navbar-header col-sm-3">
            <span class="navbar-brand">AI_BD</span>
        </div>
        <div class=" col-sm-3 col-sm-offset-6">
            <ul class="nav navbar-nav">
                <li><router-link to="/main"><span class="glyphicon glyphicon-home"></span><span>首页</span></router-link></li>
                <li><router-link to="/userindex"><span class="glyphicon glyphicon-user"></span>个人中心</router-link></li>
                <li><router-link to=""><span class="glyphicon glyphicon-cog"></span>设置</router-link></li>
                <li><a :href="HOST+'loginOut.action'" @click="loginOut"><span class="glyphicon glyphicon-off"></span>注销</a></li>
            </ul>
        </div>
    </div>
</nav>
</template>
<script>
    export default {
        name:"topbar",
        methods:{
            loginOut(){
                var vm=this;
                vm.$store.commit("SET_IS_LOGIN",false);
                vm.$store.commit("REMOVE_USER");
            }
        }
    }
</script>
<style scoped>
.topbar_nav{
    background: rgba(10, 107, 197, 0.81);
    border-color: rgba(0, 0, 0, 0.35);
    border-top:none;
    color: #111010;
}
.topbar_nav a{
    color: #FFFFFF!important;
}
.topbar_nav a:hover{
    color: #2B2727!important;
}
</style>




