<template>
  <div>
    <div id="user_data" class="container">
      <!--信息不完整下补全信息-->
      <div class="panel panel-info" v-if="!indexData">
        <div class="panel-heading">
          <h2 class="panel-title">补全资料</h2>
        </div>
        <div class="panel-body">
          <h4 class="text-primary">请补全您的信息，方便获取系统更多的功能</h4>
        </div>

        <form class="form form-horizontal" name="myaddForm" ref="myaddForm">
          <!--错误显示-->
          <div class="form-group"></div>
          <div class="form-group">
            <label class="control-label col-sm-2">邮箱</label>
            <div class="col-sm-10" :class="{'has-success':!errors.has('email')&&userAdd.email!='','has-error':errors.has('email')}">
              <div class="col-sm-6">
                <input type="text" class="form-control" v-model="userAdd.email" name="email" v-validate="'required|email'" data-vv-as="邮箱">
              </div>
              <div class="col-sm-4 text-alert text-danger">
                <span v-show="errors.has('email')">{{errors.first("email")}}</span>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-2">姓名</label>
            <div class="col-sm-10" :class="{'has-success':!errors.has('name')&&userAdd.name!='','has-error':errors.has('name')}">
              <div class="col-sm-6">
                <input type="text" class="form-control" v-model="userAdd.name" name="name" v-validate="'required|regex:^[\u4e00-\u9fa10a-zA-Z]+$'"
                  data-vv-as="姓名">
              </div>
              <div class="col-sm-4 text-alert text-danger">
                <span v-show="errors.has('name')">{{errors.first("name")}}</span>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label class="control-label col-sm-2">身份证号</label>
            <div class="col-sm-10" :class="{'has-success':!errors.has('idnumber')&&userAdd.idnumber!='','has-error':errors.has('idnumber')}">
              <div class="col-sm-6">
                <input type="text" class="form-control" v-model="userAdd.idnumber" name="idnumber" v-validate="'required|idnumber'" data-vv-as="身份证号">
              </div>
              <div class="col-sm-4 text-alert text-danger">
                <span v-show="errors.has('idnumber')">{{errors.first("idnumber")}}</span>
              </div>
            </div>
          </div>
          <div class="form-group">
            <button type="button" class="btn btn-info col-sm-2 col-sm-offset-4" @click="submitForm">提 交</button>
          </div>
        </form>
      </div>
      <!--信息完整下显示信息-->
      <div class="panel panel-info" v-if="indexData">
        <div class="panel-heading">
          <h2 class="panel-title">基本资料</h2>
        </div>
        <div class="panel-body">
          <div class="col-sm-12">
            <div class="col-sm-1 pull-right ">
              <button type="button" class=" btn btn-info" @click="updateshow.upDate=!updateshow.upDate;loginData()">编辑资料</button>
            </div>
            <div class="col-sm-1 pull-right">
              <button ype="button" class="btn btn-info" @click="updateshow.upDatePass=!updateshow.upDatePass;loginData()">修改密码</button>
            </div>
            <div class="col-sm-1 pull-right">
              <button ype="button" class="btn btn-info" @click="updateshow.upDateRole=!updateshow.upDateRole;loginData()">角色审核</button>
            </div>
          </div>
          <div class="col-sm-12">
            <ul class="nav center-block nav-list">
              <li>
                <span class="col-sm-2">帐号:</span>
                <span v-text="userData.id"></span>
              </li>
              <li>
                <span class="col-sm-2">昵称:</span>
                <span v-text="userData.nic"></span>
              </li>
              <li>
                <span class="col-sm-2">姓名:</span>
                <span v-text="userData.name"></span>
              </li>
              <li>
                <span class="col-sm-2">电话:</span>
                <span v-text="userData.tel"></span>
              </li>
              <li>
                <span class="col-sm-2">邮箱:</span>
                <span v-text="userData.email"></span>
              </li>
              <li>
                <span class="col-sm-2">站区:</span>
                <span v-text="userData.depart"></span>
              </li>
              <li>
                <span class="col-sm-2">角色:</span>
                <span v-text="userData.role"></span>
              </li>
              <li>
                <span class="col-sm-2">身份证号:</span>
                <span v-text="userData.idnumber"></span>
              </li>
              <li>
                <span class="col-sm-2">注册时间:</span>
                <span>{{userData.regtime,'yyyyMMdd'|datafilter}}</span>
              </li>
              <!--| date:'yyyy年MM月dd日'-->
            </ul>
          </div>
        </div>
        <div class="back_box" v-if="updateshow.upDateRole">
          <div class="panel panel-info col-sm-6 col-sm-offset-3 ">
            <div class="panel-heading">
              <h2 class="panel-title">角色选择 <span class="close glyphicon glyphicon-remove" @click="updateshow.upDateRole=!updateshow.upDateRole"></span></h2>
              
            </div>
            <table class="table table-bordered table-striped table-hover">
              <thead>
                <tr>
                  <th>
                    <input type="checkbox" name="" id="">
                  </th>
                  <th v-for=" (title,index) in role.titleData" :key="index" v-text="title"></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(datas,index) in role.alldatas" :key="index">
                  <!-- <td v-for=" (data,index) in datas" :key="index" v-if="index!='createok'">
                    <input type="checkbox" name="" id="" @change="updateCheckData(datas,$event)" v-model="datas.createok" v-if="index=='id'">
                    <span v-else v-html="data"></span>
                  </td> -->
                  <td><input type="checkbox" name="" id="" @change="updateCheckData(datas,$event)" v-model="datas.createok"></td>
                  <td>{{datas.name}}</td>
                  <td>{{datas.detail}}</td>
                </tr>
              </tbody>
            </table>
            <div class="form-group">
              <button type="button" class="btn btn-info  col-sm-3 col-sm-offset-2" @click="UpdateData('updateRole')">提 交</button>
            </div>
          </div>
        </div>
        <div class="back_box " v-if="updateshow.upDatePass">
          <div class="panel panel-info col-sm-6 col-sm-offset-3 ">
            <div class="panel-heading">
              <h2 class="panel-title">修改信息
              <span class="close glyphicon glyphicon-remove" @click="updateshow.upDatePass=!updateshow.upDatePass"></span></h2>
            </div>
            <form class="form form-horizontal panel-body" name="myFormPass">
              <div class="form-group">
                <label class="col-sm-3 control-label">旧密码</label>
                <div class="col-sm-8" :class="{'has-success':!errors.has('myoldPass')&&updateData.oldPass!=null,'has-error':errors.has('myoldPass')}">
                  <div class="col-sm-12">
                    <input type="password" name="myoldPass" v-model="updateData.oldPass" placeholder="请输入旧密码" class="form-control"
                      data-vv-as="密码" v-validate="'required'">
                  </div>
                </div>
                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                  <span v-show="errors.has('myoldPass')">{{errors.first('myoldPass')}}</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-sm-3 control-label">新密码</label>
                <div class="col-sm-8" :class="{'has-success':!errors.has('newPass')&&updateData.newpass!=null,'has-error':errors.has('newPass')}">
                  <div class="col-sm-12">
                    <input type="password" v-model="updateData.newpass" name="newPass" placeholder="请输入新密码" class="form-control"
                      data-vv-as="密码" v-validate="'required|password'">
                  </div>
                </div>
                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                  <span v-show="errors.has('newPass')">{{ errors.first('newPass')}}</span>
                </div>
                <div class="col-sm-10 col-sm-offset-3" style="font-size:8px;">密码为字母、数字和符号两种及以上的组合，6-16个字符</div>
              </div>
              <div class="form-group">
                <label class="col-sm-3 control-label">确认密码</label>
                <div class="col-sm-8" :class="{'has-success':!errors.has('mynewPass')&&updateData.newpass==updateData.newPass&&updateData.newPass!=null,'has-error':errors.has('mynewPass')||updateData.newpass!=updateData.newPass}">
                  <div class="col-sm-12">
                    <input type="password" name="mynewPass" v-model="updateData.newPass" placeholder="请再次输入新密码" class="form-control"
                      data-vv-as="密码" v-validate="'required'">
                  </div>
                </div>
                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                  <span v-show="errors.has('mynewPass')">{{ errors.first('mynewPass')}}</span>
                  <span v-show="updateData.newpass!=updateData.newPass&&updateData.newPass!=null">两次密码不符</span>
                </div>
              </div>
              <div class="form-group">
                <button type="button" class="btn btn-info  col-sm-3 col-sm-offset-4" @click="UpdateData('updatePwd')">提 交</button>
              </div>
            </form>
          </div>
        </div>
        <div class="back_box " v-if="updateshow.upDate">
          <div class="panel panel-info col-sm-6 col-sm-offset-3">
            <div class="panel-heading">
              <h2 class="panel-title">修改信息
              <span class="close glyphicon glyphicon-remove" @click="updateshow.upDate=!updateshow.upDate"></span></h2>
            </div>
            <form class="form form-horizontal panel-body" name="myForm">
              <div class="form-group">
                <label class="col-sm-3 control-label">昵称</label>
                <div class="col-sm-8" :class="{'has-success':!errors.has('myNic')&&updateData.nic!=null,'has-error':errors.has('myNic')}">
                  <div class="col-sm-12">
                    <input type="text" name="myNic" class="form-control" v-model="updateData.nic" required data-vv-as="昵称"
                      v-validate="'required|cnlength:12'">
                  </div>
                </div>
                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                  <span v-show="errors.has('myNic')">{{errors.first('myNic')}}</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-sm-3 control-label">姓名</label>
                <div class="col-sm-8" :class="{'has-success':!errors.has('myName')&&updateData.name!=null,'has-error':errors.has('myName')}">
                  <div class="col-sm-12">
                    <input type="text" name="myName" class="form-control" v-model="updateData.name" v-validate="'required|regex:^[\u4e00-\u9fa10a-zA-Z]+$'"
                      data-vv-as="姓名">
                  </div>
                </div>
                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger ">
                  <span v-show="errors.has('myName')">{{errors.first("myName")}}</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-sm-3 control-label">邮箱</label>
                <div class="col-sm-8" :class="{'has-success':!errors.has('myEmail')&&updateData.email!=null,'has-error':errors.has('myEmail')}">
                  <div class="col-sm-12">
                    <input type="email" name="myEmail" class="form-control" v-model="updateData.email" required data-vv-as="邮箱"
                      v-validate="'required|email'">
                  </div>
                </div>
                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger ">
                  <span v-show="errors.has('myEmail')">{{errors.first('myEmail')}}</span>
                </div>
              </div>
              <div class="form-group">
                <button type="button" class="btn btn-info  col-sm-3 col-sm-offset-4" @click="UpdateData('updateDate')">提 交</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      indexData: true, //默认为false
      userAdd: {
        role: "",
        idnumber: "",
        name: "",
        email: ""
      },
      updateshow: {
        upDate: false,
        upDatePass: false,
        upDateRole: false
      },
      userData: {},
      updateData: { adddata: [], deletedata: [] },
      role: {
        titleData: ["角色名", "描述"],
        alldatas: [{id:"1",name:1,detail:2}],
        haddatas: ["1"]
      }
    };
  },
  mounted: function() {
    var vm = this;
    vm.initData();
  },
  methods: {
    //数据提交事件
    submitForm() {
      var vm = this;
      var userAdd = vm.userAdd;
      vm.$validator.validateAll().then(function(value) {
        if (value) {
          if (userAdd.role != "") {
            vm.$axios
              .post(vm.HOST + "addData.action", vm.userAdd)
              .then(success => {
                if (success.data.code == "0") {
                  vm.$layer.msg("补全成功", {
                    icon: 1
                  });
                } else {
                  vm.$layer.msg(success.data.msg, {
                    icon: 2
                  });
                }
              })
              .catch(error => {});
          } else {
            vm.$layer.msg("请选择角色", {
              icon: 0
            });
          }
        }
      });
    },
    //更新修改数据初始数据
    loginData() {
      this.updateData = this.copeElement(this.userData);
    },
    //复制对象
    copeElement(obj) {
      var data = {};
      for (var key in obj) {
        data[key] = obj[key];
      }
      return data;
    },
    //判断数据是否完整
    initData() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "getUserDate.action")
        .then(function(success) {
          var res = success.data;
          if (res.code == "0") {
            var data = success.data;
            if (data.indexData == false) {
              vm.indexData = false;
            } else if (data.indexData == true) {
              vm.userData = success.data;
              vm.indexData = true;
            }
            vm.loginRoleSelectData(vm);
          } else {
            vm.$layer.msg("初始化信息失败，请检查登录状态", {
              icon: 5
            });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //生成选中状态
    createok() {
      var vm = this;
      var index = 0;
      var alldatas = vm.role.alldatas;
      var haddatas = vm.role.haddatas;
      if (alldatas instanceof Array) {
        alldatas.forEach(function(datas) {
          if (haddatas instanceof Array) {
            haddatas.forEach(function(data) {
              if (datas.id == data) {
                alldatas[index].createok = true;
              }
            });
          }
          index += 1;
        });
      }
    },
    //修改选中状态
    updateCheckData(data, e) {
      var vm = this;
      var data = data;
      var checkdata = vm.data;
      var checkad = e.target.checked;
      data.id += "";
      if (checkad) {
        if (!checkdata.addData.indexOf(data.id) > -1) {
          if (checkdata.deleteData.indexOf(data.id) > -1) {
            checkdata.deleteData = checkdata.deleteData.splice(data.id, 1);
          }
          if (!(vm.page.haddatas.indexOf(data.id) > -1)) {
            checkdata.addData.push(data.id);
          }
        }
      } else {
        if (!checkdata.deleteData.indexOf(data.id) > -1) {
          if (checkdata.addData.indexOf(data.id) > -1) {
            checkdata.addData = checkdata.addData.splice(data.id, 1);
          }
          if (vm.page.haddatas.indexOf(data.id) > -1) {
            checkdata.deleteData.push(data.id);
          }
        }
      }
    },
    //获取角色
    loginRoleSelectData() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "allRole.action")
        .then(function(success) {
          var res = success.data;
          if (res.code == "0") {
            vm.role.alldatas = res.data.alldatas;
            vm.role.haddatas = res.data.haddatas;
            vm.createok();
          }
        })
        .catch(function(err) {
          vm.$layer.msg("网络错误，请检查网络是否畅通", {
            icon: 5
          });
        });
    },
    //修改除角色以外信息
    UpdateData(title) {
      var vm = this;
      var data = {
        title: title,
        data: vm.updateData
      };
      vm.$validator.validateAll().then(function(value) {
        if (value) {
          switch (title) {
              case 'updateRole':
              http("UserRole.action",data);
              break;
            case "updateDate":
              http("UserDate.action",data);
              break;
            case "updatePwd":
              if (vm.updateData.newpass == vm.updateData.newPass) {
                http("UserDate.action",data);
              }
              break;
          }
        }
      });

      function http(url,data) {
        vm.$axios
          .post(vm.HOST + url, data)
          .then(function(success) {
            var res = success.data;
            if (res.code == "0") {
              vm.loginData();
              for (var key in vm.updateshow) {
                vm.updateshow[key] = false;
              }
              vm.$layer.msg("修改成功", {
                icon: 1
              });
              vm.initData();
            } else {
              vm.$layer.msg(res.msg, {
                icon: 2
              });
            }
          })
          .catch(function(err) {
            console.log(err);
            vm.$layer.msg("网络错误，请检查网络是否畅通", {
              icon: 5
            });
          });
      }
    }
  }
};
</script>
<style lang="css" scoped>
.nav-list > li {
  font-size: 2rem;
  font-family: "黑体";
  height: 4rem;
  line-height: 4rem;
}

#user_data {
  position: relative;
}

#user_data.container {
  padding: 0;
}

.back_box {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 95%;
  padding-bottom: 25px;
  background: rgba(178, 180, 182, 0.25);
}

.back_box > div {
  position: relative;
  top: 2rem;
  padding: 0;
}

.back_box form {
  background: #fff;
}

.back_box .text-alert {
  text-align: center;
}
</style>



